import React,{Component} from 'react';
import "./Table.css";
import {variables} from '../Variables.js';

export class EditBanner extends Component{
    // Do not Touch Section A
    constructor(props){
        super(props);

        this.state={
            Banners:[],
            ImgId:0,
            Imgcaption:"",
            modalTitle:"",
            Bannerimg:"empty.png",
            PhotoPath:variables.IMG_URL,
        }
    }
    
    refreshList(){
        fetch(variables.API_URL+'BannerImages')
        .then(response=>response.json())
        .then(data=>{
            this.setState({Banners:data});
        });
    }

    changeImgcaption = (e)=>{
        this.setState({Imgcaption:e.target.value});
    }

    componentDidMount(){
        this.refreshList();
    }

    addClick(){
        this.setState({
            modalTitle:"Add Banner",
            ImgId:0,
            Imgcaption:"",
            Bannerimg:"empty.png",
        });
    }

    editClick(BI){
        this.setState({
            modalTitle:"Edit Banner",
            ImgId:BI.ImgId,
            Imgcaption:BI.Imgcaption,
            Bannerimg:BI.Bannerimg,    
        });
    }

    createClick(){
        fetch(variables.API_URL+'BannerImages',{
            method:'POST',headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
        body:JSON.stringify({
            Imgcaption:this.state.Imgcaption,
            Bannerimg:this.state.Bannerimg,   
        })
    })
    .then(res=>res.json())
    .then((result)=>{
        alert(result);
        this.refreshList();
    },(error)=>{
        alert('Failed');
    })
    }

    updateClick(){
        fetch(variables.API_URL+'BannerImages',{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
            ImgId:this.state.ImgId,
            Imgcaption:this.state.Imgcaption,
            Bannerimg:this.state.Bannerimg,                    
            })            
        })
        .then(res=>res.json())
        .then((result)=>{
            alert(result);
            this.refreshList();
        },(error)=>{
            alert('Failed');
        })
    }

    deleteClick(id){
        if(window.confirm('Are you sure?')){
            fetch(variables.API_URL+'BannerImages/'+id,{
                method:'DELETE',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                }
            })
            .then(res=>res.json())
            .then((result)=>{
                alert(result);
                this.refreshList();
            },(error)=>{
                alert('Failed');
            })
            }
    }

    imageUpload=(e)=>{
        e.preventDefault();

        const formData=new FormData();
        formData.append("file",e.target.files[0],e.target.files[0].name);

        fetch(variables.API_URL+'BannerImages/savefile',{
            method:'POST',
            body:formData
        })
        .then(res=>res.json())
        .then(data=>{
            this.setState({Bannerimg:data});
        })
    }
    render(){
        const{Banners,ImgId,modalTitle,Imgcaption,Bannerimg,PhotoPath}=this.state;
        // End of Section A
        return(
            <div>
              <table className="table table-striped">
                <caption style={{color:"Black", captionSide:"top", alignContent:"start"}}><b style={{fontSize:"30px"}}>Banner</b><button type="button"
                className="btn btn-primary m-2 float-end"
                data-bs-toggle="modal"
                data-bs-target="#exampleModal"
                onClick={()=>this.addClick()}>
                    + Banner
                </button></caption>                                                                                                             
                    <tbody>
                        {Banners.map(BI=>
                            <tr key={BI.ImgId} style={{verticalAlign:"middle"}}>                                                                
                                <td><img width="300px" height="150px"  src={PhotoPath+BI.Bannerimg} alt=""/></td>
                                <td>Name: {BI.Imgcaption} ({BI.ImgId})</td>
                                <td> <button type="button"
                                    className="btn btn-light mr-1"
                                    data-bs-toggle="modal"
                                    data-bs-target="#exampleModal"
                                    onClick={()=>this.editClick(BI)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square" viewBox="0 0 16 16">
                                        <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                                        <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                                        </svg>
                                    </button>

                                    <button type="button"
                                    className="btn btn-light mr-1"
                                    onClick={()=>this.deleteClick(BI.ImgId)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill" viewBox="0 0 16 16">
                                        <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z"/>
                                        </svg>
                                    </button>
                                    </td>                               
                                </tr>
                            )}
                    </tbody>
                </table>
                <div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
                    <div className="modal-dialog modal-lg modal-dialog-centered">
                        <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">{modalTitle}</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            ></button>
                        </div>

                        <div className="modal-body">
                            <div className="d-flex flex-row bd-highlight mb-3">
                            
                            <div className="p-2 w-100 bd-highlight">
                            <div className="p-2 w-50 bd-highlight">
                                <img width="500px" height="350px" src={PhotoPath+Bannerimg} alt=""/>
                                <input className="m-2" type="file" onChange={this.imageUpload}/>
                            </div>
                                <div className="input-group mb-3">
                                    <span className="input-group-text">Caption</span>
                                    <input type="text" className="form-control"
                                    onChange={this.changeImgcaption} value={Imgcaption}/>
                                </div>
                        </div>
                        </div>

                            {ImgId===0?
                                <button type="button" data-bs-dismiss="modal"
                                className="btn btn-primary float-end"
                                onClick={()=>this.createClick()}
                                >Create</button>
                                :null}

                                {ImgId!==0?
                                <button type="button" data-bs-dismiss="modal"
                                className="btn btn-primary float-end"
                                onClick={()=>this.updateClick()}
                                >Update</button>
                                :null}
                        </div>

                </div>
                </div> 
                </div>
            </div>
        )
    }
}